
.movie__img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.movie__thumb {
    width: 160px;
    height: 240px;
    overflow: hidden;
}

.movie__title {
    padding: 4px;
    font-size: 14px;
    font-weight: 700;
    text-decoration: underline;
    
    color: #333;
}